@tailwind base;
@tailwind components;
@tailwind utilities;

:root {
  color-scheme: dark;
}

html {
  scroll-behavior: smooth;
}

body {
  @apply bg-background text-white font-sans antialiased;
}

/* Custom scrollbar for the dashboard feel */
* {
  scrollbar-width: thin;
  scrollbar-color: rgba(59, 130, 246, 0.6) transparent;
}

*::-webkit-scrollbar {
  width: 8px;
}

*::-webkit-scrollbar-track {
  background: transparent;
}

*::-webkit-scrollbar-thumb {
  background: linear-gradient(to bottom, #3b82f6, #1d4ed8);
  border-radius: 999px;
}

.glass-panel {
  @apply bg-surface/70 border border-white/5 shadow-glow-soft backdrop-blur-md;
}

.section-container {
  @apply max-w-6xl mx-auto px-4 sm:px-6 lg:px-8;
}

.section-title {
  @apply font-display text-xl sm:text-2xl tracking-[0.2em] uppercase text-muted;
}

.neon-text {
  text-shadow: 0 0 18px rgba(59, 130, 246, 0.6);
}

.hero-gradient {
  background:
    radial-gradient(circle at top left, rgba(59, 130, 246, 0.25), transparent 55%),
    radial-gradient(circle at top right, rgba(37, 99, 235, 0.18), transparent 60%),
    radial-gradient(circle at bottom, rgba(0, 0, 0, 0.9), #0a0a0a 70%);
}

.scroll-indicator {
  position: relative;
}

.scroll-indicator::after {
  content: "";
  position: absolute;
  inset: 0;
  border-radius: 999px;
  border: 1px solid rgba(148, 163, 184, 0.4);
  box-shadow: 0 0 16px rgba(59, 130, 246, 0.5);
}

.background-grid {
  position: fixed;
  inset: 0;
  z-index: -20;
  opacity: 0.3;
  background-image: linear-gradient(
      rgba(31, 41, 55, 0.5) 1px,
      transparent 1px
    ),
    linear-gradient(90deg, rgba(31, 41, 55, 0.5) 1px, transparent 1px);
  background-size: 40px 40px;
  mask-image: radial-gradient(circle at top, black, transparent 65%);
}

.cursor-glow {
  pointer-events: none;
  position: fixed;
  width: 220px;
  height: 220px;
  border-radius: 9999px;
  background: radial-gradient(circle, rgba(59, 130, 246, 0.18), transparent);
  mix-blend-mode: screen;
  z-index: -10;
}

.contact-card input,
.contact-card textarea {
  @apply w-full rounded-2xl border border-white/10 bg-black/40 px-3 py-2 text-sm text-white placeholder:text-muted focus:outline-none focus:ring-2 focus:ring-accent focus:border-accent/60 transition-all;
}

.btn-primary {
  @apply inline-flex items-center justify-center rounded-2xl bg-accent px-5 py-2 text-xs font-semibold uppercase tracking-[0.2em] text-white shadow-glow hover:bg-blue-500 transition-transform duration-200 hover:-translate-y-0.5;
}

.toast {
  @apply fixed bottom-6 right-6 z-50 rounded-2xl bg-black/80 px-4 py-3 text-sm text-white shadow-glow opacity-0 pointer-events-none;
}

.toast-visible {
  opacity: 1;
  pointer-events: auto;
  transition: opacity 0.3s ease-out, transform 0.3s ease-out;
  transform: translateY(0);
}

